.scoreboardSettings {
    width: 90%;
    height: 50%;
    border: #60d7f5 3px solid;
    border-radius: 10px;
    margin: 10px auto;
    text-align: center;
    flex: 3;
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    /*font-weight: 700;*/
    /*font-size: 100px;*/
    color: #60d7f5;
    justify-content: center;
}

.startValue {
    display: flex;
    justify-content: space-between;
    margin: 0 10px;
}

.startValue label {
    min-width: 70px;
}

.startValue input {
    font-size: 20px;
    text-align: center;
}

.maxValue {
    display: flex;
    justify-content: space-between;
    margin: 0 10px;
}

.maxValue label {
    min-width: 70px;
}

.maxValue input {
    font-size: 20px;
    text-align: center;
}